import './styles/WeatherWidget.scss';
import React, { useEffect, useState } from 'react';
import { LocationData, fetchWeatherData, UnitType, DEFAULT_UNIT } from './utils/Weather';
import { WeatherTiles } from './components/WeatherTiles';
import { CurrentWeather } from './components/CurrentWeather';
import { LocationPicker } from './components/LocationPicker';

function WeatherWidget() {
  const [units, setUnits] = useState<UnitType>(DEFAULT_UNIT);
  const [location, setLocation] = useState<LocationData>();
  const [weatherData, setWeatherData] = useState<any>();
  const [error, setError]  = useState<string | null>(null);

  /**
   * Fetch the weather data if location or units change
   */
  useEffect(() => {
    const fetchWeather = async () => {
      try {
        setWeatherData(await fetchWeatherData(location, units));

        if (location) {
          setError(null);
        }
      } catch (e) {
        setError(e);
      }
    }

    if (location) {
      fetchWeather();
    }
  }, [location, units]);

  /**
   * Gets the user geolocation from browser.
   */
  useEffect(() => {
    navigator.geolocation.getCurrentPosition(
      position => {
        setLocation({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude
        });
      },
      error => {
        setError(error.message);
      }
    );
  }, []);

  const Loading = () => {
    return (
      <section className="c-weather-widget">
        <LocationPicker setLocation={setLocation} />
        <p className="is-loading">
          Loading...
        </p>
      </section>
    );
  }
  
  const Error = ({ error }: { error: string }) => {
    return (
      <section className="c-weather-widget">
        <LocationPicker setLocation={setLocation} />
        <p className="is-error">
          Failed to fetch data. Please choose another location.
        </p>
        <p className="is-error">
          {`Error: ${error}`}
        </p>
      </section>
    );
  }

  if (error || weatherData?.cod) {
    return (
      <Error error={error || weatherData?.cod} />
    );
  } else if (!weatherData) {
    return (
      <Loading />
    );
  } else {
    return (
      <section className="c-weather-widget">
        <LocationPicker setLocation={setLocation} />
        <CurrentWeather currData={weatherData.current} units={units} timezone={weatherData.timezone} setUnits={setUnits} />
        <WeatherTiles dailyData={weatherData.daily} />
      </section>
    );
  }
}

export default WeatherWidget;
