import cities from 'cities.json';
import React, { FormEvent, useState } from 'react';
import { LocationData } from '../utils/Weather';
import Autosuggest from 'react-autosuggest';
import '../styles/LocationPicker.scss';

interface City {
  country: string,
  name: string,
  lat: string,
  lng: string
}

type TempPickerProps = {
  setLocation: React.Dispatch<React.SetStateAction<LocationData | undefined>>
}

/**
 * Renders location picker using react autosuggest.
 */
export const LocationPicker = ({ setLocation }: TempPickerProps) => {
  const [value, setValue] = useState('');
  const [suggestions, setSuggestions] = useState<City[]>([]);
  const cityList = (cities as City[]);

  const onChange = (event: FormEvent<HTMLElement>, { newValue }: { newValue: string }) => {
    setValue(newValue);
  };

  const inputProps = {
    placeholder: 'Enter a city...',
    value,
    onChange: onChange
  };

  /**
   * Generates suggestions for react autosuggest
   */
  const getSuggestions = (value: string): City[] => {
    if (value.length < 3) return [];
    let count = 0;
    const maxItems = 10;
    return cityList.filter((city:City) => {
      if (city.name.startsWith(value.trim()) && count < maxItems) {
        count++
        return true;
      } else {
        return false;
      }
    });
  }

  return (
    <div className="c-location-picker">
      <Autosuggest
        suggestions={suggestions}
        onSuggestionsClearRequested={() => setSuggestions([])}
        onSuggestionsFetchRequested={({ value }) => {
          setValue(value);
          setSuggestions(getSuggestions(value));
        }}
        onSuggestionSelected={(_, { suggestionValue }) => {
          const values = suggestionValue.split(',');
          const cityObj = cityList.find((city) => {
            return city.name === values[0] && city.country === values[1];
          })
          
          if (cityObj) {
            setLocation({
              latitude: Number(cityObj.lat),
              longitude: Number(cityObj.lng)
            });
          }

          setValue('');
        }}
        getSuggestionValue={suggestion => `${suggestion.name},${suggestion.country}`}
        renderSuggestion={suggestion => <span>{`${suggestion.name}, ${suggestion.country}`}</span>}
        inputProps={inputProps}
      />
    </div>
  );
}