import React from 'react';
import '../styles/CurrentWeather.scss';
import { iconURL, UnitType, UNIT_MAP } from '../utils/Weather';
import { TempPicker } from './TempPicker';

type CurrentWeatherProps = {
  currData: any,
  units: UnitType,
  timezone: string,
  setUnits: React.Dispatch<React.SetStateAction<UnitType>>,
}

export const CurrentWeather = ({ currData, units, timezone, setUnits }: CurrentWeatherProps) => {
  const date = new Date(currData.dt * 1000);
  const currDayOfWeek = date.toLocaleString('en-au', { weekday: 'long' });
  const currMonth = date.toLocaleString('en-au', { month: 'long' });
  const currDay = date.getDate();
  const city = timezone.split('/')[1]?.replace('_', ' ') || timezone.split('/')[0];

  return (
    <div className="c-current-weather">
      <div className="c-current-weather__left">
        <h2 className="c-current-weather__title">
          {city}
        </h2>
        <p className="c-current-weather__date">
          {`${currDayOfWeek}, ${currMonth} ${currDay}${suffix(currDay)}`}
        </p>

        <p className="c-current-weather__description">
          {currData.weather[0].main}
        </p>

        <div className="c-current-weather__temp">
          <img
            className="c-current-weather__icon"
            src={iconURL(currData.weather[0].icon)} alt={currData.weather[0].main}
          />
          <span className="is-temp">
            {Math.round(currData.temp)}
          </span>

          <TempPicker currUnit={units} setUnits={setUnits} />
        </div>
      </div>

      <div className="c-current-weather__right">
        <p className="c-current-weather__detail">
          <span className="is-label">
            Clouds:
          </span>
          <span>
            {` ${currData.clouds}%`}
          </span>
        </p>

        <p className="c-current-weather__detail">
          <span className="is-label">
            Humidity:
          </span>
          <span>
            {` ${currData.humidity}%`}
          </span>
        </p>

        <p className="c-current-weather__detail">
          <span className="is-label">
            Wind:
          </span>
          <span>
            {` ${Math.round(currData.wind_speed)} ${UNIT_MAP[units].SPEED} ${degToDir(currData.wind_deg)}`}
          </span>
        </p>

        <p className="c-current-weather__detail">
          <span className="is-label">
            UV index:
          </span>
          <span>
            {` ${currData.uvi}`}
          </span>
        </p>
      </div>
    </div>
  );
}

/**
 * Get suffix based on day on month
 * @param day Day of month
 * @returns Suffix string
 */
export const suffix = (day: number) => {
  // If day ends in 1, 2, 3
  const suffixArray = ["st", "nd", "rd"];
  const suffixIndex = day % 10 - 1;

  return (day >= 4 && day <= 20) || (day >= 24 && day <= 30)
    ? "th"
    : suffixArray[suffixIndex];
}

/**
 * Converts degrees to compass directions
 * @param degrees 
 * @returns 
 */
export const degToDir = (degrees: number) => {
  const intervals = 16;
  const degInterval = 360 / intervals;
  const offset = 0.5;

  const val = Math.floor((degrees / degInterval) + offset);
  const arr = ["N", "NNE", "NE", "ENE", "E", "ESE", "SE", "SSE", "S", "SSW", "SW", "WSW", "W", "WNW", "NW", "NNW"];
  return arr[(val % intervals)];
}