import '../styles/WeatherTiles.scss';
import { iconURL } from '../utils/Weather';

type WeatherTilesProps = {
  dailyData: any
}

export const WeatherTiles = ({ dailyData }: WeatherTilesProps) => {
  return (
    <div className="c-weather-tiles">
      {dailyData.map((data: any, i: number) => {
        return <WeatherTile key={data.dt} data={data} index={i}  />
      })}
    </div>
  );
}

type WeatherTileProps = {
  data: any,
  index: number
}

const WeatherTile = ({ data, index }: WeatherTileProps) => {
  const date = new Date(data.dt * 1000).toLocaleString('en-au', { weekday: 'long' });
  const minTemp = Math.round(data.temp.min);
  const maxTemp = Math.round(data.temp.max);
  const rainProb = Math.round(data.pop * 100);

  return (
    <div className="c-weather-tiles__tile">
      <p className="c-weather-tiles__tile-title">
        {index === 0 ? 'Today' : date}
      </p>
      <img
        className="c-weather-tiles__tile-icon"
        src={iconURL(data.weather[0].icon)} alt={data.weather[0].main}
      />
      <p className="c-weather-tiles__tile-temp">
        <span className="is-high">
          {`${maxTemp}째`}
        </span>
        <span className="is-low">
          {`${minTemp}째`}
        </span>
      </p>
      <p className="c-weather-tiles__tile-rain">
        <span className="is-label">
          Rain
        </span>
        <span className="is-percent">
          {`${rainProb}%`}
        </span>
      </p>
    </div>
  );
}