/**
 * Types
 */
export interface LocationData {
  latitude: number,
  longitude: number
}

type UnitMap = {
  [key in UnitType]: {
    SHORT_TEXT: string,
    LONG_TEXT: string,
    TYPE: string,
    SPEED: string
  }
}

/**
 * Unit constants
 */
export const UNIT_CELSIUS = 0;
export const UNIT_FAHRENHEIT = 1;
export const DEFAULT_UNIT = UNIT_CELSIUS;
export const UNIT_MAP: UnitMap = {
  [UNIT_CELSIUS]: {
    SHORT_TEXT: '째C',
    LONG_TEXT: 'Celsius',
    TYPE: 'metric',
    SPEED: 'm/s'
  },
  [UNIT_FAHRENHEIT]: {
    SHORT_TEXT: '째F',
    LONG_TEXT: 'Fahrenheit',
    TYPE: 'imperial',
    SPEED: 'mi/h'
  }
}

export type UnitType = typeof UNIT_CELSIUS | typeof UNIT_FAHRENHEIT;


/**
 * Weather API 
 */
export const fetchWeatherData = async (location: LocationData | undefined, units: UnitType) => {
  if (!location) return null;

  const response = await fetch(
    `${API_URL}?lat=${location?.latitude}&lon=${location?.longitude}&exclude=minutely,hourly,alerts&appid=${API_KEY}&units=${UNIT_MAP[units].TYPE}`);
  const json = await response.json();
  return json;
}

/**
 * Get icon url from weather api
 */
export const iconURL = (icon: string) => {
  return `http://openweathermap.org/img/wn/${icon}@2x.png`;
}

const API_URL = 'https://api.openweathermap.org/data/2.5/onecall';

// For demo purposes only
const API_KEY = '7bd4ff0e5412d0412ae1d1ff7b7102e0';
