import React from "react";
import { UnitType, UNIT_CELSIUS, UNIT_FAHRENHEIT, UNIT_MAP } from "../utils/Weather";

type TempPickerProps = {
  currUnit: UnitType,
  setUnits: React.Dispatch<React.SetStateAction<UnitType>>,
}

export const TempPicker = ({ currUnit, setUnits }: TempPickerProps) => {
  const unitList: UnitType[] = [UNIT_CELSIUS, UNIT_FAHRENHEIT];

  return (
    <>
      {unitList.map((unit: UnitType, i) => {
        if (currUnit === unit) {
          return <CurrentUnit key={unit} currUnit={unit} />
        } else {
          return <ToggleUnit key={unit} newUnit={unit} setUnits={setUnits} />
        }
      })}
    </>
  );
}

const CurrentUnit = ({ currUnit }: { currUnit: UnitType }) => {
  return (
    <span className="is-units">
      {UNIT_MAP[currUnit].SHORT_TEXT}
    </span>
  );
}

/**
 * Renders the button to switch to other unit
 */
const ToggleUnit = ({ newUnit, setUnits }: { newUnit: UnitType, setUnits: React.Dispatch<React.SetStateAction<UnitType>> }) => {
  return (
    <button onClick={_ => setUnits(newUnit)} className="is-units is-toggle" aria-label={`Switch to ${UNIT_MAP[newUnit].TYPE}`}>
      {UNIT_MAP[newUnit].SHORT_TEXT}
    </button>
  )
}
